<template>
  <div>
    <van-datetime-picker
      v-model="currentDate"
      type="date"
      title="选择年月日"
      :min-date="minDate"
      :max-date="maxDate"
      @cancel="onCancel=$emit('close')"
      @confirm="onConfirm"
    />
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
import dayjs from 'dayjs'
export default {
  name: 'UpdateBirthday',
  props: {
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      minDate: new Date(1900, 1, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(this.value),
      birthday: this.value
    }
  },
  methods: {
    // 确认生日修改
    async onConfirm (value) {
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止点击背景
        duration: 0 // 持续展示toast
      })
      // console.log(value, 'bir')
      // 临时生日变量，使用dayjs格式化时间
      const birTemp = dayjs(value).format('YYYY-MM-DD')
      // console.log(birTemp)
      try {
        await updateUserProfileAPI({
          birthday: birTemp
        })

        // 更新视图
        this.$emit('input', birTemp)
        // 生日修改成功时关闭弹出层
        this.$emit('close')
        this.$toast('保存成功')
      } catch (err) {
        this.$toast('更新失败')
      }
    }
  }
}
</script>

