<template>
  <div class="user-profile">
    <!-- 导航栏 -->
    <van-nav-bar
      title="个人信息"
      left-arrow
      @click-left="$router.back()"
      fixed
    />

    <!-- 个人信息区域 -->
    <div class="main-wrap">
      <!-- 通过 $refs 获取设置了ref 属性的元素的所有方法。绑定上点击事件-->
      <van-cell title="头像" is-link @click="$refs['file'].click()">
        <template #default>
          <van-image
            class="avatar"
            round
            fit="cover"
            :src="user.photo"
          />
        </template>
      </van-cell>
      <van-cell title="昵称" is-link :value="user.name" @click="isShowUpdateName = true"></van-cell>
      <van-cell title="性别" is-link :value="user.gender === 0 ? '男' : '女'" @click="isShowUpdateGender = true"></van-cell>
      <van-cell title="生日" is-link :value="user.birthday" @click="isShowUpdateBirthday = true"></van-cell>
    </div>

    <!-- 编辑昵称 弹出层-->
    <van-popup
      v-model="isShowUpdateName"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <UpdateName @close="isShowUpdateName = false" v-model="user.name"></UpdateName>
    </van-popup>

    <!-- 编辑性别 弹出层 -->
    <van-popup
      v-model="isShowUpdateGender"
      position="bottom"
      :style="{ height: '50%' }"
    >
      <UpdateGender @close="isShowUpdateGender = false" v-model="user.gender"></UpdateGender>
    </van-popup>

    <!-- 编辑生日 弹出层 -->
    <van-popup
      v-model="isShowUpdateBirthday"
      position="bottom"
      :style="{ height: '50%' }"
    >
      <UpdateBirthday @close="isShowUpdateBirthday = false" v-model="user.birthday"></UpdateBirthday>
    </van-popup>

    <!-- 上传头像 -->
    <!-- 上传控件隐藏，但是可以触发 -->
    <input type="file" hidden ref="file" @change="onFileChange" />
    <!-- 编辑头像 弹出层 -->
    <van-popup
      v-model="isShowUpdatePhoto"
      position="bottom"
      :style="{ height: '100%' }"
    >

      <!-- v-model="image" 是子组件裁剪用上传的图片 -->
      <!-- 也可以使用 @update-photo="user.photo = $event" 使用默认事件接受子组件传递的数据, 直接触发-不需要写触发的事件 onUpdatePhoto -->
      <UpdatePhoto
        @close="isShowUpdatePhoto = false"
        v-model="image"
        v-if="isShowUpdatePhoto"
        @update-photo="onUpdatePhoto"
      ></UpdatePhoto>
    </van-popup>

  </div>
</template>

<script>
import { getUserProfileAPI } from '../../api/index.js'
import UpdateName from './components/update-name.vue'
import UpdateGender from './components/update-gender.vue'
import UpdateBirthday from './components/update-birthday.vue'
import UpdatePhoto from './components/update-photo.vue'

export default {
  name: 'userProfile',
  components: {
    UpdateName,
    UpdateGender,
    UpdateBirthday,
    UpdatePhoto

  },

  data () {
    return {
      user: {},
      isShowUpdateName: false,
      isShowUpdateGender: false,
      isShowUpdateBirthday: false,
      isShowUpdatePhoto: false,
      image: null
    }
  },

  created () {
    this.loadProfile()
  },

  methods: {
    async loadProfile () {
      try {
        const { data: res } = await getUserProfileAPI()
        this.user = res.data
        console.log(this.user, 'user')
      } catch (err) {
        this.$toast('数据获取失败 ')
      }
    },

    // 上传图片
    onFileChange () {
      // 获取文件对象
      const file = this.$refs.file.files[0]
      // console.log(this.$refs.file.files)

      // 基于文件回去blob数据
      this.image = window.URL.createObjectURL(file)

      // 上传完成后清空控件的value值, 防止后续的操作受影响
      this.$refs.file.value = ''

      // 显示预览图像编辑弹出层
      this.isShowUpdatePhoto = true
    },

    // 接受子组件传递的图片，修改user中的photo属性
    onUpdatePhoto (newPhoto) {
      this.user.photo = newPhoto
    }
  }
}
</script>

<style lang="less" scoped>
.user-profile {
  .main-wrap {
    padding-top: 92px;
  }

  .avatar {
    width: 52px;
    height: 52px;
  }
}
</style>
