<template>
  <div class="update-name">
    <!-- 头部导航 -->
    <van-nav-bar
      title="编辑昵称"
      left-text="取消"
      right-text="确认"
      @click-left="$emit('close')"
      @click-right="onConfirm"
    />

    <!-- 输入框内容 -->
    <div class="update-name-field">
      <van-field
        v-model.trim="localName"
        rows="2"
        autosize
        type="textarea"
        maxlength="10"
        placeholder="请输入昵称"
        show-word-limit
      />
    </div>
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
export default {
  name: 'UpdateName',
  props: {
    value: { // 通过v-model 传递过来昵称, 后面可以$emit('input')来进行修改
      type: String,
      default: ''
    }
  },
  data () {
    return {
      localName: this.value
    }
  },

  methods: {
    // 确认昵称修改
    async onConfirm () {
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止点击背景
        duration: 0 // 持续展示toast
      })
      try {
        // 输入框为空验证
        if (!this.localName.length) {
          this.$toast('昵称不能为空')
          return
        }
        await updateUserProfileAPI({
          name: this.localName
        })

        // 更新昵称视图内容 - v-model 方式传给父级更新显示
        this.$emit('input', this.localName)

        // 昵称修改成功时关闭弹出层
        this.$emit('close')
        this.$toast.sccess('保存成功')
      } catch (err) {
        if (err.response && err.response.status === 409) {
          this.$toast.fail('用户名已存在')
        } else {
          this.$toast('更新失败')
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.update-name {
  .van-nav-bar {
    background-color: #fff;
    :deep(.van-nav-bar__title) {
      color: #000;
    }

    .van-icon {
      color: #3296fa;
    }
  }

  .update-name-field {
    margin: 20px;
  }
}
</style>
