<template>
  <div class="update-gender">
    <!-- 性别选择 -->
      <van-picker
        title="编辑昵称"
        show-toolbar
        :columns="columns"
        @confirm="onConfirm"
        @cancel="onCancel=$emit('close')"
        @change="onChange"
        :default-index="value"
      />
  </div>
</template>

<script>
import { updateUserProfileAPI } from '../../../api/index.js'
/*
  01 与功能相近的业务组件进行对比, 讲一些业务上的数据处理共同点直接移植过来
  02 查阅ui组件, 选择与改业务相贴切的组件, 并且阅读属性与事件说明文档, 选出该业务相关的属性与方法实施到代码中来
  03 在页面上实际使用, 去修改业务中的部分逻辑中的变量或属性来适应当前业务
  04 查看页面结果欧是否需要优化,数据流传(组件之间传递)是否需要修改
*/

export default {
  name: 'UpdateGender',
  props: {
    value: {
      type: Number,
      default: 0
    }
  },

  data () {
    return {
      columns: ['男', '女'],
      gender: this.value
    }
  },

  methods: {
    // 确认性别修改
    async onConfirm () {
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止点击背景
        duration: 0 // 持续展示toast
      })
      try {
        await updateUserProfileAPI({
          gender: this.gender
        })

        // 更新视图
        this.$emit('input', this.gender)
        // 性别修改成功时关闭弹出层
        this.$emit('close')
        this.$toast('保存成功')
      } catch (err) {
        this.$toast('更新失败')
      }
    },

    onChange (picker, value, index) {
      /*
        picker 组件实例
        value 选中的值
        index 对应的索引

      */
      this.gender = index
      console.log(index, '123')
    }
  }
}
</script>

<style lang="less" scoped>
.update-gender {
  .van-nav-bar {
    background-color: #fff;
    :deep(.van-nav-bar__title) {
      color: #000;
    }

    .van-icon {
      color: #3296fa;
    }
  }

  .update-name-field {
    margin: 20px;
  }

  .van-button {
    background-color: #fff;
    color: #000;
    border: none;
  }
}
</style>
