<template>
  <div class="update-photo">
    <img :src=value class="img" ref="img">
    <div class="toolbar">
      <span class="cancel" @click="$emit('close')">取消</span>
      <span class="confirm" @click="onConfirm">完成</span>
    </div>
  </div>
</template>

<script>
import { updateUserPhotoAPI } from '../../../api/index.js'

import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'

export default {
  name: 'UpdatePhoto',
  props: {
    // 父组件传递过来的数据类型为object但并不是基本对象类型({})， 默认值建议设为null
    value: { // 头像是blob数据，为字符串
      type: String,
      default: ''
    }
  },

  data () {
    return {
      cropper: null
    }
  },
  mounted () {
    this.initCropper()
  },
  methods: {
    initCropper () {
      // 获取图片页面元素
      const image = this.$refs.img
      this.cropper = new Cropper(image, {
        viewMode: 1, // 定义裁剪模式，0：裁剪框可以扩展到画布外， 1,2,3会将裁剪框限制在画布内
        dragMode: 'move', // 拖动模式 move: 移动画布
        aspectRatio: 1, // 截图比例, 1:1可以简写为1
        autoCropArea: 1, // 自动截图区
        cropBoxMovable: false, // 截图区域是否可以移动
        cropBoxResizable: false, // 截图区的缩放功能
        background: false, // 是否显示默认背景
        movable: true // 背景图片是否可以移动，默认true
        // crop (event) {
        // const eventObj = { // 当移动图片是会返回的各种数据（今天的案例中不需要使用）
        //     x: event.detail.x,
        //     y: event.detail.y,
        //     width: event.detail.width,
        //     height: event.detail.height,
        //     rotate: event.detail.rotate,
        //     scaleX: event.detail.scaleX,
        //     scaleY: event.detail.scaleY,
        //     detail: event.detail
        //   }
        //   console.log(eventObj)
        // }
      })
    },

    onConfirm () {
      // 裁剪方案一： 将裁剪坐标通过接口发送给后端
      // 01 获取坐标
      console.log(this.cropper.getData())
      // 02 将获取的坐标发送给后端

      // 裁剪方案二：前端通过cropperjs实现图像裁剪, 将裁剪好的图片发送给后端保存
      // 通过 this.cropper.getCroppedCanvas().toBlob() 方法流程，让裁剪区域中的图像进行处理
      this.cropper.getCroppedCanvas().toBlob((blob) => {
        console.log(blob)
        // 获取到图像blob对象后, 调用接口将图像传递给后端
        this.updateUserPhoto(blob)
      })
    },

    async updateUserPhoto (blob) {
      const formData = new FormData()
      formData.append('photo', blob)

      // 设置一个加载事件, 防止用户持续点击背景等
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 禁止点击背景
        duration: 0 // 持续展示toast
      })

      try {
        const { data: res } = await updateUserPhotoAPI(formData)

        // 提示成功
        this.$toast.success('更新头像成功')

        // 更新视窗
        // 通过接口获取上传后的返回数据来获取当前的头像 - 然后再传递给父组件进行修改视图
        this.$emit('update-photo', res.data.photo)

        // 关闭弹出层
        this.$emit('close')
      } catch (err) {
        this.$toast.fail('更新头像失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.update-photo {
  background-color: #000;
  height: 100%;

  .toolbar {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;

    .cancel, .confirm {
      font-size: 40px;
      margin: 12px;
    }
  }

  .img {
    display: block;
    max-width: 100%;
  }
}
</style>
